<template>
  <div>
    <Header name="提交订单"></Header>
    <div class="card-context-order">
      <div class="getAddress">
        <div v-if="addRessData && addRessData.id" class="addressMsg" @click="getAddRessList">
          <div class="addressPerson">
            <img
              :src="require('@/assets/image/lbs.png')"
              alt=""
              class="addressImg"
            />
            <div class="top">
              <p>收货人：<span class="concrete">{{addRessData.name}}&nbsp;&nbsp;{{addRessData.phoneNumber}}</span></p>
            </div>
            <div class="bottom">
              <p class="adress">收货地址：</p>
              <p class="concrete">
                {{addRessData.province}} {{addRessData.city}} {{addRessData.region}} {{addRessData.town}}{{addRessData.detailAddress}}
              </p>
            </div>
          </div>
          <div class="rightArrow"><van-icon name="arrow" /></div>
        </div>
        <div v-else class="joinAddress" @click="getAddRessList">选择地址</div>
        <img :src="require('@/assets/image/line.png')" alt="" class="bottomimg"/>
      </div>
      <div class="buyMsg">
        <div class="topMsg" v-for="(item,i) in productList" :key="i">
          <div class="waresMsg">
            <div class="waresImage">
              <img :src="item.productPic" style="height: 86px; width: 86px;" alt="" />
            </div>
            <div class="waresWord">
              <div class="ttop">
                {{item.productName}}
                <div class="shopSpecs" v-if="item.productAttr">
                  {{JSON.parse(item.productAttr)[0] && JSON.parse(item.productAttr)[0].value}}&nbsp;&nbsp;
                  {{JSON.parse(item.productAttr)[1] && JSON.parse(item.productAttr)[1].value}}<br/>
                  {{JSON.parse(item.productAttr)[2] && JSON.parse(item.productAttr)[2].value}}&nbsp;&nbsp;
                  {{JSON.parse(item.productAttr)[3] && JSON.parse(item.productAttr)[3].value}}
                </div>
              </div>
              <div class="ccenter" v-if="item.memberDiscount">享{{accMul(item.memberDiscount, 10)}}折</div>
              <div class="bbottom">
                <div class="amount" v-if="integrationBuy === 1"><span>{{item.price}}</span> <b  style="font-size: 12px;color:#666">积分</b></div>
                <div class="amount" v-else>￥<span>{{item.price}}</span></div>
                <div class="number">x{{item.quantity}}</div>
              </div>
            </div>
          </div>
        </div>
        <div class="centerMsg" v-if="integrationBuy === 1" style="height:70px">
          <div class="content01">
            <p>商品所需积分</p>
            <p style="color:#111;font-size:16px;font-weight:bold">{{priceData.totalAmount}}</p>
          </div>
          <div class="content01">
            <p>可用积分</p>
            <p style="color:#111;font-size:16px;font-weight:bold">{{current}}</p>
          </div>
          <div class="content01">
            <p>剩余积分</p>
            <p style="color:#111;font-size:16px;font-weight:bold">{{parseInt(current - priceData.totalAmount)}}</p>
          </div>
        </div>
        <div class="centerMsg" v-else>
          <div class="content01">
            <p>商品总金额</p>
            <p>￥{{priceData.totalAmount}}</p>
          </div>
          <div class="content01">
            <p>运费</p>
            <!-- {{priceData.freightAmount}} -->
            <p>￥{{Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight}}</p>
          </div>
          <div class="content01">
            <p>会员折扣</p>
            <p>{{!!priceData.promotionAmount ? '-￥' + (priceData.promotionAmount).toFixed(2) : '￥' + (priceData.promotionAmount).toFixed(2)}}</p>
          </div>
          <div class="content01">
            <p>
              <van-checkbox v-model="checked" :disabled="couponDisabled" @change="changeVal" v-if="checkMoney" shape="square" icon-size="15px" checked-color="#F48D1C">使用代金券</van-checkbox>
              <span v-else>使用代金券</span>
            </p>
            <p style="text-align:right;">
              <!-- <span v-if="checked" style="font-size:13px">剩余代金券￥{{isOnline === '1' && checked ? (checkMoney - couponBalance).toFixed(2) : priceData.payAmount > checkMoney  ? 0 : ( Number(priceData.totalAmount) >= Number(freightData.freeFreightOrderAmount) ? accSub(checkMoney, priceData.payAmount) : accSub(checkMoney, (priceData.payAmount + freightData.standardFreight)))}}</span> -->
              <span>已有代金券￥{{checkMoney}}</span></p>
          </div>
          <div class="content01" v-if="checked && isOnline === '1'">
            <p>自定义代金券使用数额: </p>
            <input v-model="couponBalance" type="number" class="inp" @blur="blur" @input="couponInsert"/>
          </div>
        </div>
        <div class="bottomMsg" v-if="integrationBuy === 1">
          <div class="deduction">本次扣除积分&emsp;<span>{{ checked ? payMoney: priceData.payAmount}}</span></div>
        </div>
        <div class="bottomMsg" v-else>
          <div class="shopDis">
            <!-- {{ checked ? payMoney: priceData.payAmount}} -->
            已优惠&nbsp;<span>{{(isOnline === '0' ? (checked ? yh : priceData.promotionAmount) : (checked ? (couponBalance === '' ? Number(priceData.promotionAmount) : Number(couponBalance) + Number(priceData.promotionAmount)) : priceData.promotionAmount)).toFixed(2)}}</span>&nbsp;元&emsp;
            共{{productList.length}}件&emsp;
            <span>
              ￥{{
                (isOnline === '0' ? 
                (Number(checked ? payMoney : priceData.payAmount) + (Number(checked ? payMoney : priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 : (Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight)))
                : 
                (priceData.payAmount - credit - couponBalance) === 0 ? 
                priceData.freightAmount
                : 
                Number((priceData.payAmount - credit - couponBalance).toFixed(2)) < 0 ? 
                (Number(checked ? payMoney : priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 : (Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight))
                : 
                (priceData.payAmount - credit - couponBalance).toFixed(2)).toFixed(2)
              }}
            </span>
          </div>
          <div class="getIntegral">可获得积分：<span>
            {{
              Math.floor((isOnline === '0' ? 
              (Number(checked ? payMoney : priceData.payAmount) + (Number(checked ? payMoney : priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 : (Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight)))
              : 
              (priceData.payAmount - credit - couponBalance) === 0 ? 
              priceData.freightAmount
              : 
              Number((priceData.payAmount - credit - couponBalance).toFixed(2)) < 0 ? 
              (Number(checked ? payMoney : priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 : (Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight))
              : 
              (priceData.payAmount - credit - couponBalance).toFixed(2)) - (Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight))
            }}
          </span></div>
        </div>
        <div style="height: 19px" v-if="!integrationBuy"></div>
      </div>
      <!-- <div class="loan" v-if="!integrationBuy">
        <div class="top">
          <p>付款方式</p>
        </div>
        <div class="online">
          <van-radio-group v-model="isOnline" @change="radioChange">
            <van-cell-group style="margin: 0" :border="false">
              <van-cell title="线上付款" style="padding:10px 4.23%">
                <template #right-icon>
                  <van-radio name="0">
                    <template #icon="props">
                      <img class="img-icon" @click="onlinePay" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" />
                    </template>
                  </van-radio>
                </template>
              </van-cell>
              <van-cell title="使用信用额度" style="padding:10px 4.23%" :label="`当前可用额度为：${credit}元`">
                <template #right-icon>
                  <van-radio name="1">
                    <template #icon="props">
                      <img class="img-icon" @click="loanPay" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" />
                    </template>
                  </van-radio>
                </template>
              </van-cell>
            </van-cell-group>
          </van-radio-group>
        </div>
      </div> -->
      <!-- <div class="invoice" v-if="isOnline === '0'">
        <div class="top">
          <p>发票</p>
        </div>
        <div class="bottom" v-if="integrationBuy === 1">
          <div class="noInvoice">注：服务类商品暂时无法为您开具发票</div>
          <div></div>
        </div>
        <div class="bottom" v-else>
          <div v-if="invoiceMsgs.flag" class="invoiceMsg">
            <div class="useUser">
              <p>{{invoiceMsgs.type}}</p>
              <p>{{invoiceMsgs.name}}</p>
            </div>
            <div class="revise" @click="applyInvoice('this')" v-if="forOnce === 0">修改</div>
            <div class="revise" @click="applyInvoice('2')" v-if="forOnce === 1 || forOnce === 2">查看</div>
          </div>
          <div v-else class="invoiceMsg">
            <div class="revise" @click="applyInvoice">开票申请</div>
          </div>
          <div class="message">
            <img :src="require('@/assets/image/warn.png')" alt="" />
            <span>您需在签收确认订单后我们才能给您开出发票</span>
          </div>
        </div>
      </div> -->
      <div class="remarks">
        <div class="remarkTitle">
          <p>备注</p>
        </div>
        <div class="inpt">
          <van-field
            v-model="remarks"
            rows="2"
            clearable
            autosize
            type="textarea"
            placeholder="请输入留言"
          />
        </div>
      </div>
      <div style="height:80px"></div>
      <div class="subOrder" v-if="integrationBuy === 1" @click="payOrder">
        <div class="jiesuan">
          <p>立即结算</p>
        </div>
      </div>
      <div class="subOrder" v-else>
        <div class="leftMsg">
          共{{productList.length}}件商品&emsp;
            总计：<span>￥{{
                (isOnline === '0' ? 
                (Number(checked ? payMoney : priceData.payAmount) + (Number(checked ? payMoney : priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 : (Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight)))
                : 
                (priceData.payAmount - credit - couponBalance) === 0 ? 
                priceData.freightAmount
                : 
                Number((priceData.payAmount - credit - couponBalance).toFixed(2)) < 0 ? 
                (Number(checked ? payMoney : priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 : (Number(priceData.payAmount) >= Number(freightData.freeFreightOrderAmount) ? 0 :  freightData.standardFreight))
                : 
                (priceData.payAmount - credit - couponBalance).toFixed(2)).toFixed(2)
              }}</span>
        </div>
        <div class="rightMsg" @click="pay">提交订单</div>
      </div>
      <van-action-sheet v-model:show="payShow" title="结算" style="background: #fff; min-height: 500px;" cancel-text="取消">
        <div class="payIntegral">
          <p class="no1">应付积分</p>
          <p class="no2">{{priceData.totalAmount}}</p>
        </div>
        <div class="orderDetail">
          <p class="no0">订单详情</p>
          <p class="no1">收货信息：<span>{{addRessData.name}}&nbsp;&nbsp;{{addRessData.phoneNumber}}</span></p>
          <p class="no2">商品名称：<span v-for="(item,i) in productList" :key="i">{{item.productName}}<br/></span></p>
        </div>
        <div class="nowPay" @click="nowPay">立即支付</div>
      </van-action-sheet>
      <!-- 收货地址 -->
      <van-action-sheet v-model="showBottom" title="收货地址" style="background: #FFFFFF; min-height: 600px;">
        <div v-for="item in arList" class="card-lists" :key="item.id" @click="getFreightNum(item)">
          <dl>
            <dt @click="handleAddRess(item)">{{item.name}}<span>{{item.phoneNumber}}</span></dt>
            <dd @click="handleAddRess(item)">{{item.province}} {{item.city}} {{item.region}} {{item.town}}{{item.detailAddress}}</dd>
            <div>
              <div @click="updateDto(item)" style="display: inline;">
                <img :src="item.defaultStatus ? require('@/assets/image/radio.png') : require('@/assets/image/radio_gray.png')">
                默认地址
              </div>
              <!-- <img style="float: right; width: 20px; margin-left: 10px;" :src="require('@/assets/image/edit.png')" @click="addOrEdit(item)">
              <van-icon @click="deleteDto(item)" name="delete"/>
               -->
              <van-icon @click="addOrEdit(item)" name="edit" style="margin-left: 15px;"/>
              <van-icon @click="deleteDto(item)" name="delete-o"/>
            </div>
          </dl>
        </div>
        <div class="card-add" @click="addAddRessInfo">
          + 添加地址
        </div>
      </van-action-sheet>

      <van-action-sheet v-model="showTop" style="background: #FFFFFF; min-height: 600px;" title="收货地址">
        <div class="modify-content">
          <van-form @submit="onSubmit">
            <van-field
              v-model="form.name"
              name="收货人"
              label="收货人"
              placeholder="请填写收货人姓名"
              :rules="[{ required: true, message: '请填写收货人' }]"/>
              <!-- :rules="[{ required: true, message: '请填写收货人' }]" -->
            <van-field
              v-model="form.phoneNumber"
              name="手机号码"
              label="手机号码"
              placeholder="请填写收货人手机号码"
              :rules="[{ required: true, message: '请填写手机号码' }]"/>
            <van-field
              readonly
              clickable
              name="所在地区"
              :value="form.value"
              label="所在地区"
              placeholder="省市区县、乡镇等"
              @click="showArea = true"
              :rules="[{ required: true, message: '请填写所在地区' }]"
            />
            <van-popup v-model="showArea" position="bottom">
              <van-area
                :area-list="areaList"
                @confirm="onConfirm"
                @cancel="showArea = false"
              />
            </van-popup>
            <van-field
              v-model="form.detailAddress"
              name="详细地址"
              label="详细地址"
              rows="2"
              type="textarea"
              placeholder="街道、楼牌号"
              :rules="[{ required: true, message: '请填写详细地址' }]"/>
            <div>
            <dl class="df">
              <dt>设置默认地址</dt>
              <dd>提醒：每次下单会默认推荐使用该地址</dd>
              <div class="top"><van-switch v-model="checkedAadd" /></div>
            </dl>
            </div>
            <div style="margin: 40px 0px;">
              <van-button round block type="info" native-type="submit">{{form.id ? '确认' : '保存并使用'}}</van-button>
            </div>
          </van-form>
        </div>
      </van-action-sheet>

      <!-- 申请开票 -->
      <van-action-sheet v-if="showInvoice" v-model="showInvoice" style="background: #FFFFFF; min-height: 600px;" title="发票信息">
        <van-tabs v-model="active" class="invoice-context-info">
          <van-tab title="电子普通发票" name="a">
            <div class="invoiceBody">
              <van-radio-group v-model="formfm.type" direction="horizontal" style="margin-bottom: 15px;">
                <van-radio name="1" @click="clickRadio('1')">个人
                  <template #icon="props">
                    <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                  </template>
                </van-radio>
                <van-radio name="2" @click="clickRadio('2')">单位
                  <template #icon="props">
                    <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                  </template>
                </van-radio>
              </van-radio-group>
              <div class="personInp" v-if="formfm.type === '1'">
                <van-form @submit="submitBtnFormfm" ref='formfm'>
                  <van-field
                    v-model="formfm.invoiceTitle"
                    name="发票抬头"
                    label="发票抬头"
                    placeholder="请输入发票抬头"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formfm.email"
                    type="email"
                    name="邮箱"
                    label="邮箱"
                    placeholder="请输入正常使用的邮箱"
                  />
                  <van-button class="paymentBtn" round block type="info" native-type="submit" :disabled="forOnce === 2">确认</van-button>
                </van-form>
              </div>
              <div class="companyInp" v-if="formfm.type === '2'">
                <van-form @submit="submitBtnFormfm" ref='formfm'>
                  <van-field
                    v-model="formfm.invoiceTitle"
                    name="发票抬头"
                    label="发票抬头"
                    placeholder="请输入发票抬头"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formfm.taxpayerId"
                    type="taxpayerId"
                    name="纳税人识别号"
                    label="纳税人识别号"
                    placeholder="请按税务登记证填写"
                  />
                  <van-field
                    v-model="formfm.email"
                    type="email"
                    name="邮箱"
                    label="邮箱"
                    placeholder="请输入正常使用的邮箱"
                  />
                  <van-button class="paymentBtn" round block type="info" native-type="submit" :disabled="forOnce === 2">确认</van-button>
                </van-form>
              </div>
              <div class="tishi">
                <img :src="require('@/assets/image/warn.png')" alt="" width="12px" height="12px"/>
                <p>申请发票24小时后，即可在您的邮箱中下载电子普通发票。</p>
              </div>
            </div>
          </van-tab>
          <van-tab title="专用发票" name="b">
            <div class="purpose">
              <div class="topFont">
                <div class="condition">
                  开具专用发票的需订单金额至少累计<span>10000</span>元起
                </div>
                <div class="operation">
                  <!-- {{ order.amount }} -->
                  <div class="inLeft">当前订单金额<span>{{numberVal}}</span>元</div>
                  <div class="inRight" @click="toOrderList">
                    <span class="toMerge">合并其他订单金额&nbsp;&gt;</span>
                  </div>
                </div>
                <div style="height: 8px"></div>
              </div>
    
              <div class="entryMsg" v-if="invChoice">
                <van-form @submit="nextStep" ref='formin'>
                  <van-field
                    v-model="formin.invoiceTitle"
                    name="单位名称"
                    label="单位名称"
                    placeholder="请输入单位名称"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.taxpayerId"
                    name="纳税人识别号"
                    label="纳税人识别号"
                    placeholder="请输入纳税人识别号"
                  />
                  <van-field
                    v-model="formin.companyAddress"
                    name="注册地址"
                    label="注册地址"
                    placeholder="注册地址"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.telphone"
                    name="注册电话"
                    label="注册电话"
                    placeholder="注册电话"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.bankName"
                    name="开户银行"
                    label="开户银行"
                    placeholder="请输入开户银行"
                    :rules="[{ required: true }]"
                  />
                  <van-field
                    v-model="formin.bankAccount"
                    name="银行账户"
                    label="银行账户"
                    placeholder="请输入银行账户"
                    :rules="[{ required: true }]"
                  />
                  <van-button class="next" round block type="info" native-type="submit">下一步</van-button>
                </van-form>
              </div>
              <div class="entryLast" v-else>
                <div class="entryList">
                  <van-radio-group v-model="radio"  direction="horizontal">
                    <van-radio name="1" @click="clickLocal('1')">同收货地址
                      <template #icon="props">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                      </template>
                    </van-radio>
                    <van-radio name="2" @click="clickLocal('2')">上次邮寄地址
                      <template #icon="props">
                        <img class="img-icon" :src="props.checked ? require('@/assets/image/yxz.png') : require('@/assets/image/wxz.png')" width="20px" height="20px"/>
                      </template>
                    </van-radio>
                  </van-radio-group>
                </div>
                <van-form  @submit="submitOrder" ref='formin'>
                  <van-field
                      v-model="formin.recipient"
                      name="姓名"
                      label="姓名"
                      placeholder="请输入姓名"
                      :rules="[{ required: true }]"
                    />
                    <van-field
                      v-model="formin.recipientPhone"
                      name="手机号"
                      label="手机号"
                      placeholder="请输入手机号"
                      :rules="[{ required: true }]"
                    />
                    <van-field
                      readonly
                      clickable
                      v-model="formin.value"
                      name="所在地区"
                      label="所在地区"
                      placeholder="选择省-市-区-街道"
                      :rules="[{ required: true }]"
                      @click="showArea = true"
                    />
                    <van-popup v-model="showArea" position="bottom">
                      <van-area
                        :area-list="areaList"
                        @confirm="onConfirmInvoice"
                        @cancel="showArea = false"
                      />
                    </van-popup>
                    <van-field
                      v-model="formin.detailAddress"
                      name="详细地址"
                      label="详细地址"
                      placeholder="请输入详细地址"
                      :rules="[{ required: true }]"
                    />
                  <div class="last">
                    <div class="before" @click="backStep">上一步</div>
                    <van-button class="subform" round block type="info" native-type="submit" :disabled="forOnce === 2">确定</van-button>
                  </div>
                </van-form>
              </div>
              <div class="bottomMsg">
                <img :src="require('@/assets/image/warn.png')" alt="" width="12px" height="12px"/>
                <p>
                  专票在订单签收后8-10个工作日开具并寄出，顺丰寄送，一般会在您订单完成后15个工作日内送达给您（如遇节假日稍有延迟）。请注意查收；增值税专用发票收到后请妥善保存，如退货请一同寄回，如退货专票未能寄回，则需扣除相应的税点。
                </p>
              </div>
            </div>
          </van-tab>
        </van-tabs>

        
        <!-- 发票选订单 -->
        <van-action-sheet v-model="showOrder" @cancel="handleOrder" style="background: #f6f6f6; min-height: 600px;" title="未开发票订单">
          <van-sticky>
            <div class="topMost">
              <div class="topBody">
                <van-checkbox
                  v-model="checkAll"
                  shape="square"
                  checked-color="#f00"
                  icon-size="16px"
                  @click="selectClickAll"
                  ><span style="color: #666">选择开票订单</span></van-checkbox
                >
                <!-- {{order.amount}} -->
                <p>已选择订单金额 <span>{{numberVal}}</span> 元</p>
              </div>
            </div>
          </van-sticky>
          <div class="orderMsg">
            <!-- 下面填写v-for -->
            <div class="onceWares" v-for="item in order.data" :key="item.id">
              <div v-if="item.orderItemList.length > 0">
                <div class="topMsg">
                <p>订单号：{{item.orderSn}}</p>
                <p class="timer">{{item.createTime}}</p>
                </div>
                <div class="waresName">
                <van-checkbox v-model="item.visible" @click="orderClickDto(item)" shape="square" checked-color="#f00" :disabled="orderId + '' === item.id || item.kpName === '退货中' || item.kpNum === 0">
                  <div class="waresBody" v-for="term in item.orderItemList" :key="term.id">
                    <div class="wareImage">
                      <img :src="term.productPic" alt="" />
                    </div>
                    <div class="wareTitle">
                      <div class="title">{{term.productName}}</div>
                      <div class="deploy" v-if="term.productAttr">
                        <p v-if="JSON.parse(term.productAttr)[0]">{{JSON.parse(term.productAttr)[0].value}}</p>
                        <p v-if="JSON.parse(term.productAttr)[1]">{{JSON.parse(term.productAttr)[1].value}}</p>
                      </div>
                      <div class="money">￥<span>{{term.productPrice}}</span></div>
                    </div>
                  </div>
                </van-checkbox>
                </div>
                <div class="allPrice">
                  <span>合计: <span class="font">￥{{item.payAmount}}</span></span>
                </div>
              </div>
            </div>
            <div class="confirmBtn" @click="handleOrder">确定</div>
          </div>
        </van-action-sheet>
      </van-action-sheet>
    </div>
  </div>
</template>

<script>

import * as api from '@/data/api/market/index'
import Header from '@/components/Layout/header/index'
import { areaList } from '@vant/area-data'

export default {
  components :{
    Header
  },
  data() {
    return {
      forOnce:0,
      credit: 0,
      isOnline: '0',
      payShow: false,
      current: 0,
      integrationBuy: +this.$route.query.integrationBuy,
      checked:false,
      productList: [],
      priceData: {
        payAmount: 0,
        promotionAmount: 0
      },
      checkMoney: 0,
      changeMoney: 0,
      payMoney: 0,
      memberReceiveAddressList: [],
      addRessData: {},
      invoice: '',
      yh: 0.00,
      // 收货地址
      form: {
        id: '',
        name: '',
        phoneNumber: '',
        value: '',
        detailAddress: '',
        memberId: "",
        defaultStatus: 0,
        optionPhone: "",
        postCode: "",
        province: "",
        provinceId: "",
        city: "",
        cityId: "",
        region: "",
        regionId: "",
        town: "",
        townId: "",
        createTime: "",
        updateTime: ""
      },
      showBottom: false,
      showTop: false,
      arList: [],
      checkedAadd: true,
      showArea: false,
      areaList,
      // 发票信息
      showInvoice : false,
      radio:"1",
      active: "a",
      value:'',
      showArea:false,
      formin: {
        type: 3,
        value: '',
        invoiceTitle: '',
        taxpayerId: '',
        companyAddress: '',
        telphone: '',
        bankName: '',
        bankAccount: '',
        recipient: '',
        recipientPhone: '',
        detailAddress: '',
        cityId: '',
        provinceId: '',
        regionId: '',
        townId: '',
        city: '',
        province: '',
        region: '',
        town: '',
        orderIds: [], //订单
        invoiceAmount: '' //订单金额
      },
      invChoice: true,
      formfm: {
        type: "1",
        invoiceTitle: '个人',
        taxpayerId: '',
        email: '',
        orderIds: '', //订单
        invoiceAmount: '' //订单金额
      },
      invoiceMsgs: {
        type: '',
        name: '',
        flag: false
      },
      showOrder: false,
      checkAll: false,
      remarks:'',
      order: {
        params: {
          pageNum: 1,
          pageSize: 50,
          total: 0,
          invoiced: false,
          status: -1
        },
        first: 5000,
        amount: 0,
        data: []
      },
      freightData: {
        id: null,
        provinceId: null,
        province: null,
        standardFreight: null,
        freeFreightOrderAmount: null,
        createTime: null
      },
      numberVal: 0,
      couponBalance: null,
      couponDisabled: false
    }
  },
  // filters:{
  //   dateFilter: function (data, format = "") {
  //     if (data) {
  //       var dt = new Date(data);
  //       var y = dt.getFullYear();
  //       var m = (dt.getMonth()+1).toString().padStart(2,"0");
  //       var d = dt.getDate().toString().padStart(2,"0");
  //       var h = dt.getHours().toString().padStart(2,"0");
  //       var mm = dt.getMinutes().toString().padStart(2,"0");
  //       var ss = dt.getSeconds().toString().padStart(2,"0");;  
  //       return `${y}-${m}-${d} ${h}:${mm}:${ss}`;
  //     } else {
  //       return '-'
  //     }
  //   }
  // },
  mounted(){
    this.getProductlist()
    this.memberIntegration()
  },
  methods:{
    couponInsert() {
      if(this.checkMoney > this.priceData.payAmount){
        if(this.couponBalance > this.priceData.payAmount) {
          // if(Number(this.priceData.totalAmount) >= Number(this.freightData.freeFreightOrderAmount)) {
            this.couponBalance = this.priceData.payAmount
          // } else {
          //   this.couponBalance = Number(this.priceData.payAmount) + Number(this.freightData.standardFreight)
          // }
          this.payMoney = (this.priceData.payAmount - this.couponBalance - this.credit).toFixed(2)
        } else {
          this.payMoney = (this.priceData.payAmount - this.couponBalance - this.credit).toFixed(2)
        }
      } else {
        if(this.couponBalance > this.checkMoney) {
          this.couponBalance = this.checkMoney
          this.payMoney = (this.priceData.payAmount - this.couponBalance).toFixed(2)
        } else {
          this.payMoney = (this.priceData.payAmount - this.couponBalance).toFixed(2)
        }
      }
    },
    onlinePay() {
      this.isOnline === '0'
      this.checked = false
      this.couponDisabled = false
    },
    blur() {
      if(this.couponBalance === '' && this.priceData.payAmount > this.credit) {
        this.couponBalance = Number((this.priceData.payAmount - this.credit).toFixed(2))
        this.payMoney = (this.priceData.payAmount - this.couponBalance).toFixed(2)
      }
      if(this.couponBalance < Number((this.priceData.payAmount - this.credit).toFixed(2))) {
        this.couponBalance = Number((this.priceData.payAmount - this.credit).toFixed(2))
      }
    },
    radioChange() {
      if((this.credit + this.checkMoney) < this.priceData.payAmount){
        this.isOnline = '0'
        return;
      }
    },
    loanPay(){
      if((this.credit + this.checkMoney) < this.priceData.payAmount){
        this.$notify({
          message: '信用额度和代金券余额不足',
          color: '#ad0000',
          background: '#ffe1e1',
        })
        return;
      }
      this.isOnline = '1'
      this.checked = false
      if(this.credit < this.priceData.payAmount) {
        this.checked = true
        this.couponDisabled = true
      }
    },
    nowPay(){
      let me = this
      // if(me.numVal > me.current){
      //   me.$notify({
      //     message: '积分不足，无法购买',
      //     color: '#ad0000',
      //     background: '#ffe1e1',
      //   })
      //   return;
      // }
      let form = ''
      localStorage.setItem('historyUrl', '/user/orderdetails')
      if (me.invoice) {
        form = {
          cartIds: localStorage.getItem('cartIds') && localStorage.getItem('cartIds').split(','),
          memberReceiveAddressId: me.addRessData.id,
          orderInvoice: me.invoice,
          useVoucher: me.checked,
          note:me.remarks
        }
      } else {
        form = {
          cartIds: localStorage.getItem('cartIds') && localStorage.getItem('cartIds').split(','),
          memberReceiveAddressId: me.addRessData.id,
          useVoucher: me.checked,
          note:me.remarks
        }
      }
      if(form.memberReceiveAddressId){
        if (me.$route.query.productSkuId || me.$route.query.productId) {
        form.cartIds = [me.$route.query.id]
        form.productSkuId = me.$route.query.productSkuId
        form.productSkuCode = me.$route.query.productSkuCode
        form.quantity = me.$route.query.quantity
        form.productId = me.$route.query.productId
        form.payType = ''

        api.integralBuyShop({memberReceiveAddressId: form.memberReceiveAddressId, note: form.note, productSkuCode: form.productSkuCode, quantity: form.quantity}).then(res => {
          if (res.code === 200) {
              me.$notify({ type: 'success', message: '下单成功，订单号：' + res.data.order.orderSn })
              me.$router.push({
                path:'/user/paySuccess',
                query: {
                  orderNum: res.data.order.orderSn
                }
              })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
      }else{
        me.$notify({
          message: '请选择收货地址',
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    payOrder(){
      this.payShow = true
    },
    memberIntegration () {
      let me = this
      api.memberIntegration().then(res => {
        if (res.code === 200) {
          me.current = res.data.current
          me.credit = res.data.credit ? res.data.credit : 0
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    },
    handleAddRess (ar) {
      this.addRessData = ar
      this.showBottom = false
    },
    addAddRessInfo () {
      this.form = {
        id: '',
        name: '',
        phoneNumber: '',
        value: '',
        detailAddress: '',
        memberId: "",
        defaultStatus: 0,
        optionPhone: "",
        postCode: "",
        province: "",
        provinceId: "",
        city: "",
        cityId: "",
        region: "",
        regionId: "",
        town: "",
        townId: "",
        createTime: "",
        updateTime: ""
      }
      this.showTop = true
    },
    updateDto (item) {
      let me = this
      let data = {
        defaultStatus: item.defaultStatus === 1 ? 0 : 1,
        id: item.id,
        memberId: item.memberId
      }
      api.updateDefaultStatus(data).then(res => {
        if (res.code === 200) {
          me.getAddRessList()
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
      })
    },
    deleteDto (item) {
      let me = this
      me.$dialog.confirm({
        message: '您确认要删除该地址吗？',
      }).then(() => {
        api.addressDelete({id: item.id}).then(res => {
          if (res.code === 200) {
            me.getAddRessList()
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }).catch(() => {
      });
    },
    getProductlist () {
      let me = this
      if (me.$route.query.productSkuId || me.$route.query.productId) {
        let opform = {
          cartIds: [me.$route.query.id],
          memberReceiveAddressId: '',
          orderSn: '',
          payType: '',
          productSkuCode: me.$route.query.productSkuCode,
          productId: me.$route.query.productId,
          productSkuId: me.$route.query.productSkuId,
          quantity: me.$route.query.quantity
        }
        api.generateConfirmOrderBySku(opform).then(res => {
          if (res.code === 200) {
            me.productList = res.data.cartPromotionItemList
            me.priceData = res.data.calcAmount
            me.priceData.payAmount = me.priceData.payAmount ? me.priceData.payAmount : 0
            me.checkMoney = res.data.voucherBlance
            me.memberReceiveAddressList = res.data.memberReceiveAddressList
            res.data.memberReceiveAddressList.map(item => {
              if (item.defaultStatus === 1) {
                me.addRessData = item
                me.getFreightNum(item)
              }
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      } else {
        let ids = localStorage.getItem('cartIds') && localStorage.getItem('cartIds').split(',')
        api.generateConfirmOrder(ids).then(res => {
          if (res.code === 200) {
            me.productList = res.data.cartPromotionItemList
            me.priceData = res.data.calcAmount
            me.priceData.payAmount = me.priceData.payAmount ? me.priceData.payAmount : 0
            me.checkMoney = res.data.voucherBlance
            me.memberReceiveAddressList = res.data.memberReceiveAddressList
            res.data.memberReceiveAddressList.map(item => {
              if (item.defaultStatus === 1) {
                me.addRessData = item
                me.getFreightNum(item)
              }
            })
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
    },
    getFreightNum (row) {
      let self = this
      api.getFreightSettingById(row.provinceId).then(res => {
        if (res.code === 200) {
          self.freightData = res.data
        } else {
          self.$message.error(res.message);
        }
      }).catch((res) => {
        self.$message.error(res.message);
      }).finally(() => {
      })
    },
    changeVal (evt) {
      if (this.checked) {
        this.isOnline === '1' ? this.priceData.payAmount > this.credit ? this.couponBalance = Number((this.priceData.payAmount - this.credit).toFixed(2)) : this.couponBalance = '' : null
        if (this.accSub(this.priceData.payAmount , this.checkMoney) > 0 ) {
          this.changeMoney = this.checkMoney
          this.payMoney = this.isOnline === '1' ? this.accSub(this.priceData.payAmount, this.couponBalance) : this.accSub(this.priceData.payAmount, this.checkMoney)
          this.yh = this.payMoney > 0 ? this.accAdd(this.checkMoney, this.priceData.promotionAmount) : this.priceData.promotionAmount
        } else {
          this.changeMoney = this.accSub(this.checkMoney, this.priceData.payAmount)
          this.yh = this.accSub(this.checkMoney, this.changeMoney) + this.priceData.promotionAmount
          this.payMoney = 0
        }
      }
    },
    pay () {
      let me = this
      let form = ''
      let useCredit = null
      if(me.isOnline === '0'){
        useCredit = false
      }else{
        useCredit = true
      }
      localStorage.setItem('historyUrl', '/user/orderdetails')
      if (me.invoice) {
        form = {
          cartIds: localStorage.getItem('cartIds') && localStorage.getItem('cartIds').split(','),
          memberReceiveAddressId: me.addRessData.id,
          orderInvoice: me.invoice,
          // useVoucher: me.checked ? (Number(me.priceData.payAmount) > this.checkMoney ? this.checkMoney : Number(me.priceData.payAmount))  : 0,
          isUseVoucher: me.checked ? (Number(me.priceData.payAmount) > this.checkMoney ? this.checkMoney : Number(me.priceData.payAmount))  : 0,
          note: me.remarks
        }
        // Number(me.priceData.totalAmount) > Number(me.freightData.freeFreightOrderAmount) ? 
        // (Number(me.priceData.payAmount) > this.checkMoney ? this.checkMoney : Number(me.priceData.payAmount))
        // : Number(me.priceData.payAmount) + Number(me.freightData.standardFreight))  : 0,
      } else {
        form = {
          cartIds: localStorage.getItem('cartIds') && localStorage.getItem('cartIds').split(','),
          memberReceiveAddressId: me.addRessData.id,
          // useVoucher: me.isOnline === '0' ? 
          // (me.checked ? (Number(me.priceData.payAmount) > this.checkMoney ? this.checkMoney : Number(me.priceData.payAmount)) : 0)
          //  : (me.checked ? Number(me.couponBalance) : 0),
          isUseVoucher: me.isOnline === '0' ? 
          (me.checked ? (Number(me.priceData.payAmount) > this.checkMoney ? this.checkMoney : Number(me.priceData.payAmount)) : 0)
           : (me.checked ? Number(me.couponBalance) : 0),
          note:me.remarks
        }
        // me.isOnline === '0' ? 
        // (me.checked ? (Number(me.priceData.totalAmount) > Number(me.freightData.freeFreightOrderAmount) ? (Number(me.priceData.payAmount) > this.checkMoney ? this.checkMoney : Number(me.priceData.payAmount)) : Number(me.priceData.payAmount) + Number(me.freightData.standardFreight)) : 0)
        // : (me.checked ? Number(me.couponBalance) : 0),
      }
      form.isUseVoucher = form.isUseVoucher > 0 ? 1 : 0
      if(form.memberReceiveAddressId){
        if (me.$route.query.productSkuId || me.$route.query.productId) {
        form.cartIds = [me.$route.query.id]
        form.productSkuId = me.$route.query.productSkuId
        form.productSkuCode = me.$route.query.productSkuCode
        form.quantity = me.$route.query.quantity
        form.productId = me.$route.query.productId
        form.payType = ''
        form.useCredit = useCredit
        api.generateOrderBySku(form).then(res => {
          if (res.code === 200) {
            if(useCredit){
              if (res.data.order.status === 6) {
                me.$notify({ type: 'success', message: '下单成功，订单号：' + res.data.order.orderSn })
                me.$router.push({
                  path:'/user/myorders',
                  query: {
                    'toorders':'-1',
                  }
                })
              } else {
                if(res.data.order.payAmount <= 0) {
                  me.$notify({ type: 'success', message: '下单成功，订单号：' + res.data.order.orderSn })
                  me.$router.push({
                    path:'/user/myorders',
                    query: {
                      'toorders':'-1',
                    }
                  })
                  return
                }
                me.$router.push({
                  path:'/user/payment',
                  query: {
                    order: JSON.stringify(res.data.order),
                    orderItemList : JSON.stringify(res.data.orderItemList[0]),
                    token: localStorage.getItem('token')
                  }
                })
              }
            }else{
              if (res.data.order.status === 1) {
                me.$notify({ type: 'success', message: '下单成功，订单号：' + res.data.order.orderSn })
                me.$router.push({
                  path:'/user/myorders',
                  query: {
                    'toorders':'-1',
                  }
                })
              } else {
                me.$router.push({
                  path:'/user/payment',
                  query: {
                    order: JSON.stringify(res.data.order),
                    orderItemList : JSON.stringify(res.data.orderItemList[0]),
                    token: localStorage.getItem('token')
                  }
                })
              }
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      } else {
        form.useCredit = useCredit
        api.orderGenerateOrder(form).then(res => {
          if (res.code === 200) {
            if (res.data.order.status === 6) {
              me.$notify({ type: 'success', message: '下单成功，订单号：' + res.data.order.orderSn })
              me.$router.push({
                path:'/user/myorders',
                query: {
                  'toorders':'-1',
                }
              })
            } else {
              if(res.data.order.payAmount <= 0) {
                me.$notify({ type: 'success', message: '下单成功，订单号：' + res.data.order.orderSn })
                me.$router.push({
                  path:'/user/myorders',
                  query: {
                    'toorders':'-1',
                  }
                })
                return
              }
              me.$router.push({
                path:'/user/payment',
                query: {
                  order: JSON.stringify(res.data.order),
                  orderItemList : JSON.stringify(res.data.orderItemList[0])
                }
              })
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
      }else{
        me.$notify({
          message: '请选择收货地址',
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
      // this.$router.push({
      //   path: '/user/payment'
      // })
    },
    getAddRessList(){
      let me = this
      me.showBottom = true
      api.addressList().then(res => {
        if (res.code === 200) {
          me.arList = res.data.map(item => {
            item.visible = false
            return item
          })
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
      }).finally(() => {
      })
      // this.$router.push({
      //   path:'/user/address/modify'
      // })
    },
    addOrEdit (data) {
      if (data.id) {
        this.form.id = data.id
        this.form.name = data.name
        this.form.phoneNumber = data.phoneNumber
        this.form.value = data.province + '/' + (data.city === '市辖区' ? data.province : data.city) + '/' + data.region
        this.form.detailAddress = data.detailAddress
        this.form.memberId = ""
        this.checkedAadd = !!data.defaultStatus
        this.form.defaultStatus = data.defaultStatus
        this.form.optionPhone = data.optionPhone
        this.form.createTime = data.createTime
        this.form.updateTime = data.updateTime
        this.form.province = data.province
        this.form.provinceId =data.provinceId
        this.form.city = data.city
        this.form.cityId = data.cityId
        this.form.region = data.region
        this.form.regionId = data.regionId
        this.form.town = data.town || ''
        this.form.townId = data.townId || ''
      }
      this.showTop = true
    },
    onSubmit () {
      let me = this
      me.form.defaultStatus = me.checkedAadd ? 1 : 0
      if (me.form.id) {
        api.addressUpdate(me.form).then(res => {
          if (res.code === 200) {
            // me.$router.push({
            //   path: '/user/address/index'
            // })
            me.getAddRessList()
            me.showTop = false
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      } else {
        api.addressCreate(me.form).then(res => {
          if (res.code === 200) {
            // me.$router.push({
            //   path: '/user/address/index'
            // })
            me.getAddRessList()
            me.showTop = false
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            me: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
    },
    onConfirm(values) {
      this.form.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join('/');
      let em = this
      values.map(item => {
        em.form.province = values[0].name
        em.form.provinceId = values[0].code.substring(0,2)
        em.form.city = (values[1].name === '天津市' || values[1].name === '北京市' || values[1].name === '上海市') ? '市辖区' : values[1].name
        em.form.cityId = values[1].code.substring(0,4)
        em.form.region = values[2].name
        em.form.regionId = values[2].code
      })
      this.showArea = false;
    },
    applyInvoice (parameter) {
      if(parameter === 'this'){
        this.forOnce = 1
      }else if(parameter === '2'){
        this.forOnce = 2
      }
      this.numberVal = Number(this.checked ? this.payMoney : this.priceData.payAmount) + (Number(this.checked ? this.payMoney : this.priceData.payAmount) >= Number(this.freightData.freeFreightOrderAmount) ? 0 :  (this.isOnline !== '0') ? 0 : this.freightData.standardFreight)
      this.showInvoice = true
      // this.order.amount = this.order.amount ? this.order.amount : this.checked ? this.payMoney: this.priceData.payAmount
    },
    clickRadio (evt) {
      if (evt === '2') {
        this.formfm.invoiceTitle = '';
        this.formfm.email = ''
      } else {
        this.formfm.invoiceTitle = '个人';
        this.formfm.email = ''
      }
    },
    // 个人/单位
    submitBtnFormfm () {
      let me = this
      me.formfm.invoiceAmount = me.numberVal//me.checked ? me.payMoney: me.priceData.payAmount
      console.log("个人-单位======>", me.formfm)
      me.invoice = me.formfm
      me.showInvoice = false
      me.invoiceMsgs = {
        type: '电子普通发票',
        name: me.formfm.type === '1' ? '个人' : '单位',
        flag: true
      }
    },
    onConfirmInvoice (values) {
      this.formin.value = values
        .filter((item) => !!item)
        .map((item) => item.name)
        .join('/');
      let em = this
      values.map(item => {
        em.formin.province = values[0].name
        em.formin.provinceId = values[0].code.substring(0,2)
        em.formin.city = (values[1].name === '天津市' || values[1].name === '北京市' || values[1].name === '上海市') ? '市辖区' : values[1].name
        em.formin.cityId = values[1].code.substring(0,4)
        em.formin.region = values[2].name
        em.formin.regionId = values[2].code
      })
      this.showArea = false;
    },
    nextStep () {
      this.invChoice = false
      this.clickLocal('1')
    },
    backStep () {
      this.invChoice = true;
    },
    submitOrder () {
      let me = this
      me.formin.invoiceAmount = me.order.amount
      if (me.formin.invoiceAmount >= 10000) {
        console.log("个人-单位======>", me.formin)
        me.invoice = me.formin
        me.invoice.orderIds = me.invoice.orderIds && me.invoice.orderIds.length > 0 ? me.invoice.orderIds.toString() : ''
        me.showInvoice = false
        me.invoiceMsgs = {
          type: '专用发票',
          name: me.formin.invoiceTitle,
          flag: true
        }
      } else {
        me.$notify({
          message: "专用发票：开票金额不能小于10000",
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }
    },
    clickLocal (evt) {
      let me = this
      if (evt === '1') {
        if (me.addRessData.id) {
          me.formin.recipient = me.addRessData.name
          me.formin.recipientPhone = me.addRessData.phoneNumber
          me.formin.value = me.addRessData.province + '/' + (me.addRessData.city === '市辖区' ? me.addRessData.province : me.addRessData.city) + '/' + me.addRessData.region
          me.formin.province = me.addRessData.province
          me.formin.provinceId = me.addRessData.provinceId
          me.formin.city = me.addRessData.city
          me.formin.cityId = me.addRessData.cityId
          me.formin.region = me.addRessData.region
          me.formin.regionId = me.addRessData.regionId
          me.formin.town = me.addRessData.town || ''
          me.formin.townId = me.addRessData.townId || ''
          me.formin.detailAddress = me.addRessData.detailAddress
        }
      } else {
        api.getLastInvoice({'invoiceType': 3}).then(res => {
          if (res.code === 200) {
            if (res.data) {
              me.formin.recipient = res.data.recipient
              me.formin.recipientPhone = res.data.recipientPhone
              me.formin.value = res.data.province + '/' + (res.data.city === '市辖区' ? res.data.province : res.data.city) + '/' + res.data.region
              me.formin.province = res.data.province
              me.formin.provinceId =res.data.provinceId
              me.formin.city = res.data.city
              me.formin.cityId = res.data.cityId
              me.formin.region = res.data.region
              me.formin.regionId = res.data.regionId
              me.formin.town = res.data.town || ''
              me.formin.townId = res.data.townId || ''
              me.formin.detailAddress = res.data.detailAddress
            } else {
              me.$notify({
                message: '暂无历史发票信息',
                color: '#ad0000',
                background: '#ffe1e1',
              })
            }
          } else {
            me.$notify({
              message: res.message,
              color: '#ad0000',
              background: '#ffe1e1',
            })
          }
        }).catch((res) => {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }).finally(() => {
        })
      }
    },
    toOrderList () {
      let me = this
      if(me.forOnce === 2){
        me.$notify({
          message: '不可操作',
          color: '#ad0000',
          background: '#ffe1e1',
        })
        return
      }
      api.getOrderList(me.order.params).then(res => {
        if (res.code === 200) {

          if (res.data.list) {
            // if (typeof(me.formin.orderIds) === String) {
            //   me.formin.orderIds = me.formin.orderIds.split(',')
            // }
            if (typeof(me.formin.orderIds) === 'string') {
              if (me.formin.orderIds.indexOf(',') > -1) {
                me.formin.orderIds = me.formin.orderIds.split(',')
              } else {
                me.formin.orderIds = [me.formin.orderIds]
              }
            }
            res.data.list = res.data.list.map(item => {
              item.kpNum = item.payAmount
              let flag = true
              me.formin.orderIds && me.formin.orderIds.map(od => {
                if (od === item.id) {
                  item.visible = true
                  flag = false
                }
              })
              if (flag) {
                item.visible = false
              }
            
              item.kpName = '-'
              item.returnApplyList.map(ral =>{
                if (ral.status !== 3) {
                  item.kpNum = item.kpNum - Number(ral.returnAmount)
                }
                if (ral.type === 1 && ral.status === 1) {
                  item.kpName = '退货中'
                }
              })
              item.orderItemList.forEach((term, termIndex, termArr) => {
                item.returnApplyList.forEach((itm) => {
                  if(term.id === itm.orderItemId && itm.type === 1 || itm.type === 2) {
                    item.payAmount -= term.productPrice
                    termArr.splice(termIndex, 1)
                  }
                })
              })
              return item
            })

            me.order.data = res.data.list
          }
        } else {
          me.$notify({
            message: res.message,
            color: '#ad0000',
            background: '#ffe1e1',
          })
        }
      }).catch((res) => {
        me.$notify({
          message: res.message,
          color: '#ad0000',
          background: '#ffe1e1',
        })
      }).finally(() => {
        me.showOrder = true
      })
    },
    orderClickDto (item) {
      let me = this
      if (item.visible) {
        me.numberVal = me.accAdd(Number(me.numberVal), item.kpNum).toFixed(2)
      } else {
        me.numberVal = me.accSub(Number(me.numberVal), item.kpNum).toFixed(2)
      }
    },
    selectClickAll () {
      let me =this
      if (me.checkAll) {
        me.order.data = me.order.data.map(item => {
          item.visible = true
          me.numberVal = me.accAdd(me.numberVal, item.kpNum).toFixed(2)
          return item
        })
      } else {
        me.order.data = me.order.data.map(item => {
          item.visible = false
          return item
        })
        me.numberVal = me.checked ? me.payMoney: me.priceData.payAmount
      }
    },
    handleOrder () {
      let me = this
      me.formin.orderIds = []
      // me.numberVal = me.checked ? me.payMoney: me.priceData.payAmount
      me.order.data = me.order.data.map(item => {
        if (item.visible) {
          me.formin.orderIds.push(item.id)
          // me.numberVal = me.accAdd(me.numberVal, item.payAmount).toFixed(2)
        }
        return item
      })
      me.showOrder = false
      me.showInvoice = true
    },
    accMul (arg1, arg2) {
      let m = 0,
        s1 = arg1.toString(),
        s2 = arg2.toString();
      try {
        m += s1.split('.')[1].length;
      } catch (e) {}
      try {
        m += s2.split('.')[1].length;
      } catch (e) {}
      return (
        (Number(s1.replace('.', '')) * Number(s2.replace('.', ''))) /
        Math.pow(10, m)
      )
    },
    accSub (arg1, arg2) {
      let r1, r2, m, n;
      try {
        r1 = arg1.toString().split('.')[1].length;
      } catch (e) {
        r1 = 0;
      }
      try {
        r2 = arg2.toString().split('.')[1].length;
      } catch (e) {
        r2 = 0;
      }
      m = Math.pow(10, Math.max(r1, r2));
      n = r1 >= r2 ? r1 : r2;
      return Number(((arg1 * m - arg2 * m) / m).toFixed(4));
    },
    accAdd (arg1, arg2) {
      let r1, r2, m;
      try {
        r1 = arg1.toString().split('.')[1].length;
      } catch (e) {
        r1 = 0;
      }
      try {
        r2 = arg2.toString().split('.')[1].length;
      } catch (e) {
        r2 = 0;
      }
      m = Math.pow(10, Math.max(r1, r2));
      return (arg1 * m + arg2 * m) / m;
    },
    deepClone (data) {
      const type = checkedType(data)
      let result
      if (type === 'Array') {
        result = []
      } else if (type === 'Object') {
        result = {}
      } else {
        return data
      }
      if (type === 'Array') {
        for (let i = 0, len = data.length; i < len; i++) {
          result.push(deepClone(data[i]))
        }
      } else if (type === 'Object') {
        for (const key in data) {
          result[key] = deepClone(data[key])
        }
      }
      return result
    }
  }
};
</script>

