    display: flex;
    align-items: center;
    font-size: 14px;
    color: #666666;

    span {
      font-size: 18px;
      color: #000;
      font-weight: bold;
    }
  }

  .rightMsg {
    flex: 1;
    height: 50px;
    background: #ff4c4c;
    text-align: center;
    line-height: 50px;
    color: #fff;
    font-size: 16px;
    font-weight: bold;
  }
}

/* 收货地址 */
.card-lists {
  background: #f5f5f5;
  margin: 10px;
  border-radius: 12px;
  border: 1px solid #FFFFFF;
  dl {
    margin: 18px;
    line-height: 22px;
    dt {
      margin-bottom: 5px;
      font-size: 16px;
      font-weight: bold;
      color: #222222;
      span {
        font-size: 16px;
        font-weight: bold;
        color: #222222;
        float: right;
      }
    }
    dd {
      color: #666666;
    }
    div {
      margin-top: 20px;
      img:first-child {
        width: 20px;
        vertical-align: top;
        margin-right: 10px;
        border-radius: 10px;
      }
      >>>.van-icon {
        font-size: 20px;
        float: right;
        color: #FF4C4C!important;
      }
    }
  }
}

.modify-content {
  padding: 10px 20px;
  .df {
    line-height: 30px;
    .top {
      float: right;
      margin-top: -45px;
    }
    dt {
      color: #000000;
      font-size: 14px;
      font-weight: bold;
    }
    dd {
      color: #666666;
      font-size: 13px;
    }
  }
  >>>.van-field__label {
    width: 4.2em;
    font-size: 14px;
    color: #666666;
  }
  >>>.van-cell {
    padding: 15px 5px;
  }
  >>>.van-field__control {
    font-size: 15px;
  }
  >>>.van-button--info {
    background-color: #FF4C4C;
    border: 1px solid #FF4C4C;
    border-radius: 4px!important;
    font-size: 17px!important;
    padding: 0 10px!important;
  }
  >>>.van-switch--on {
    background-color: #00DE6A;
    font-size: 25px;
  }
  >>>.van-switch {
    font-size: 25px;
  }
}

/* 发票信息 */
.invoice-context-info {
  >>>.van-ellipsis {
    display: contents;
  }
  color: #fd2744;
  line-width: 26px;
  line-height: 4px;
  .invoiceBody {
    margin: 10px 4%;
    width: 92%;
    height: 200px;
    >>>.van-radio{
      margin 20px 0px 0px 4.34%
    }
    .personInp, .companyInp {
      >>>.van-cell::after{
        width 88.4%
      }
      .paymentBtn {
        margin-top: 30px;
        width: 100%;
        height: 48px;
        text-align: center;
        line-height: 48px;
        background: #ff4c4c;
        border: 1px solid #ff4c4c;
        border-radius: 4px;
        color: #fff;
        font-weight: bold;
        font-size: 17px;
      }
    }
    .subBtn {
      margin-top: 40px;
      width: 100%;
      height: 48px;
      background: #FF4C4C;
      border-radius: 4px;
      font-size: 17px;
      color: #FFFFFF;
      text-align: center;
      line-height: 48px;
    }
    .tishi {
      margin: 20px 4px;
      width: 338px;
      height: 12px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      p {
        width: 322px;
        height: 2px;
        font-size: 10px;
        color: #8D8D8D;
      }
    }
  }

  .purpose {
    width: 100%;

    .topFont {
      margin: 20px 0px 0px 4%;
      width: 96%;//360
      height: 60px;
      border-bottom: 1px solid #eee;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .condition {
        font-size: 13px;
        color: #666666;

        span {
          color: #111;
        }
      }

      .operation {
        display: flex;
        justify-content: space-between;

        .inLeft {
          font-size: 13px;
          color: #666666;
        }

        .inRight {
          margin-right: 4.1%;

          .toMerge {
            font-size: 12px;
            color: #FF4C4C;
          }
        }
      }
    }
    .entryMsg{
      width: 94%;
      margin-left: 3%;
      .next {
        width: 100%;
        height: 48px;
        background: #FF4C4C;
        border-radius: 4px;
        text-align: center;
        line-height: 48px;
        font-size: 17px;
        color: #FFFFFF;
        margin-top: 34px;
        border: 1px solid #FF4C4C;
      }
    }
    .entryLast {
      width: 94%;
      margin-left: 3%;
      /* >>>.van-cell:first-child{
        padding 0px 0px!important
      } */
      /* >>>.van-cell:not(:first-child){
        padding 13px 16px!important
      } */
      .entryList {
        width: 100%;
        margin-left 16px
        height: 50px;
        border-bottom: 1px solid #ebedf0;
        display: flex;
        align-items: center;
      }
      .last {
        width: 95.8%;//345
        height: 48px;
        margin-top: 34px;
        display: flex;
        justify-content: space-between;

        .before, .subform {
          width: 43.5%;
          height: 48px;
          background: #FF4C4C;
          border: 1px solid #FF4C4C;
          border-radius: 4px;
          text-align: center;
          line-height: 48px;
          font-size: 17px;
          color: #FFFFFF;
        }
      }
    }

    .bottomMsg {
      margin: 20px 5.33%;
      width: 90.1%;
      display: flex;
      justify-content: space-between;
      p {
        width: 95%;
        font-size: 10px;
        color: #8D8D8D;
        line-height: 17px;
      }
    }
  }
}

.topMost {
  width: 100%;
  height: 44px;
  background: #FFFFFF;

  .topBody {
    margin: 0px 25px;
    width: 330px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

    p {
      font-size: 13px;
      color: #666666;

      span {
        color: #111;
      }
    }
  }
}

.orderMsg {
  padding: 1px 0px 33px;

  .onceWares {
    margin: 10px 10px 0px;
    // height: 180px;
    background: #FFFFFF;
    border-radius: 12px;

    .topMsg {
      width: 100%;
      height: 48px;
      border-bottom: 1px solid #f0f0f0;
      display: flex;
      justify-content: space-between;
      align-items: center;

      p {
        margin: 0px 10px;
        color: #444;
        font-size: 13px;
      }

      .timer {
        font-size: 12px;
        color: #999999;
      }
    }

    .waresName {
      padding: 15px 15px;
      width: 325px;
      // height: 132px;
      display: flex;
      align-items: center;

      .waresBody {
        margin-bottom: 10px;
        width: 300px;
        height: 86px;
        display: flex;
        justify-content: space-between;
        .wareImage img{
          height: 80px;
          width: 80px;
        }
        .wareTitle {
          width: 204px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          .title {
            max-height: 38px;
            font-size: 14px;
            color: #000000;
            font-weight: bold;
            overflow: hidden;
            /* white-space: nowrap; */
            text-overflow: ellipsis;
          }

          .deploy {
            height: 12px;
            align-items: center;
            padding-right: 5px;
            height: 12px;
            font-size: 12px;
            color: #888888;
            display: flex;
            justify-content: space-between;
          }

          .money {
            width: 87px;
            font-size: 15px;
            color: #FF2F2F;

            span {
              font-size: 21px;
              font-weight: bold;
            }
          }
        }
      }
      .waresBody:last-child{
        margin-bottom: 0px;
      }
    }
    .allPrice{
      width: 100%;
      height: 38px;
      border-top: 1px solid #f0f0f0;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      span{
        margin-right: 16px;
      }
      .font{
        margin-right: 0px;
        color: #f44;
        font-size: 20px;
        font-weight: bold;
      }
    }
  }

  .confirmBtn {
    width: 93%;
    height: 48px;
    background: #FF4C4C;
    border-radius: 4px;
    margin: 33px 15px 0px;
    font-size: 17px;
    font-weight: bold;
    color: #FFFFFF;
    text-align: center;
    line-height: 48px;
  }
}
</style>


